import "./../styles.css";
import React from "react";

export default class RecipeSearch extends React.Component {
  state = {
    recipes: [
      "Basic Mathematics",
      "Mathematics For Dummies",
      "Advanced Mathematics",
      "Basic English",
      "English For Dummies",
      "Advanced English",
      "World History",
      "US History"
    ],
    searchTerm: ""
  };

  render() {
    //pull out matching recipes for search term
    let matchedRecipes = this.state.recipes.filter((recipe) => {
      return recipe
        .toLocaleUpperCase()
        .includes(this.state.searchTerm.toLocaleUpperCase());
    });

    console.log(matchedRecipes);

    // Generate <li> elements for each of the recipes
    let recipesList = matchedRecipes.map((recipe) => {
      return <li>{recipe}</li>;
    });

    console.log(recipesList);

    return (
      <div className="recipes">
        <h2>Book Search</h2>
        <input
          value={this.state.searchTerm}
          onChange={(event) => {
            this.updateSearch(event);
          }}
        />
        <ul>{recipesList}</ul>
      </div>
    );
  }

  updateSearch(event) {
    this.setState({ searchTerm: event.target.value });
  }
}
